import React, { PureComponent } from 'react'
import PropTypes from "prop-types"
import { Card } from "react-bootstrap"

class ProfStats extends PureComponent {
    constructor(props) {
        super(props)
        this.state = {
            rating: null
        }
    }

    /** Scraping courtesy of DickyT */
    findRating(data) {
        console.log(data)
        const reader = document.implementation.createHTMLDocument('reader');
        const fakeHtml = reader.createElement('html');
        fakeHtml.innerHTML = data;

        const avgRatingElem = fakeHtml.querySelectorAll('div.container > h5')[0];
        console.log("Elem " + avgRatingElem)
        if (avgRatingElem) {            
            const matchRes = avgRatingElem.innerText.match(/Average rating: ([0-9]\.[0-9]{2})/);
            if (matchRes && matchRes[1]) {
                const avgRating = Number(matchRes[1]);
                if (!Number.isNaN(avgRating)) {
                    return avgRating
                }
            }
        }

        return null
    }

    componentDidMount() {
        console.log(this.props.prof_name)
        // console.log("https://terpscheduler.herokuapp.com/https://planetterp.com/professor/" + this.props.prof_name.split(' ').reverse().join('_'))
        // fetch("https://terpscheduler.herokuapp.com/https://planetterp.com/professor/" + this.props.prof_name.split(' ').reverse().join('_'))
        //     .then(res => res.text())
        //     .then(data => {
        //         this.setState({ rating: this.findRating(data) })
        //     })
        //     .catch(console.log)
    }

    componentDidUpdate() {
        console.log(this.props.prof_name)
        console.log("https://terpscheduler.herokuapp.com/https://planetterp.com/professor/" + this.props.prof_name[0].split(' ')[1])
        fetch("https://terpscheduler.herokuapp.com/https://planetterp.com/professor/" + this.props.prof_name[0].split(' ')[1])
            .then(res => res.text())
            .then(data => {
                this.setState({ rating: this.findRating(data) })
            })
            .catch(console.log)
    }

    render() {
        return (
            <div>
                {this.state.rating ? this.state.rating : "N/A"}
            </div>
        )
    }
}

ProfStats.propTypes = {
    prof_name: PropTypes.array,
}

export default ProfStats